<template>

    <div class="bank-offers-container">

        <div class="go-back-link">
            <router-link to="/"><span class="icon-arrow-left"></span>Назад</router-link>
        </div>

        <div class="page-title">
            <h1>Предложения банка</h1>
        </div>

        <div class="block">

            <div class="offers-row">

                <div
                    v-for="offer in offers"
                    :key="offer.id"
                    class="offer-item">
                    <div class="offer-img" :style="{clipPath: `url(#offer-${offer.id})`}">
                        <img :src="offer.imgSrc" alt="">
                        <svg height="0" width="0" preserveAspectRatio="none">
                            <defs>
                                <clipPath :id="`offer-${offer.id}`">
                                    <rect fill="#FFFFFF" rx="10" ry="10" x="0" y="0" width="100" height="100"></rect>
                                </clipPath>
                            </defs>
                        </svg>
                    </div>
                    <div class="offer-body">
                        <div class="offer-title">
                            <div class="h3">{{offer.title}}</div>
                        </div>
                        <div class="offer-descr">
                            <p>{{offer.descr}}</p>
                            <a :href="offer.link" class="btn btn-block">Перейти</a>
                        </div>
                    </div>
                </div>

            </div>

        </div>

    </div>

</template>


<script>

    import {mapState} from 'vuex'

    export default {
        name: "BankOffers",
        computed: {
            ...mapState({
                offers: state => state.otherOffers.items
            })
        }
    }
</script>

